/* src/components/CryptoTable.css */
.table-container {
    overflow-x: auto; /* For responsiveness on smaller screens */
  }
  
  table {
    width: 100%;
    border-collapse: collapse;
  }
  
  th,
  td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  th {
    background-color: #f2f2f2;
  }
  
  .name-container {
    display: flex;
    align-items: center;
  }
  
  .symbol {
    margin-right: 8px;
    font-weight: bold;
  }
  
  .sparkline {
    width: 100px;
    height: 30px;
    border: 1px solid #ccc; /* Placeholder for the chart */
  }
  
  /* Responsive adjustments (example) */
  @media (max-width: 768px) {
    table,
    thead,
    tbody,
    th,
    td,
    tr {
      display: block;
    }
  
    thead tr {
      position: absolute;
      top: -9999px;
      left: -9999px;
    }
  
    tr {
      border: 1px solid #ccc;
      margin-bottom: 1em;
    }
  
    td {
      border: none;
      border-bottom: 1px solid #eee;
      position: relative;
      padding-left: 50%;
    }
  
    td:before {
      position: absolute;
      top: 6px;
      left: 6px;
      width: 45%;
      padding-right: 10px;
      white-space: nowrap;
      font-weight: bold;
      content: attr(data-column);
    }
  }